.statsTable{
    width: 100%;
    height: 100%;
    display: flex;
    overflow-x: auto;
}
.stats-table{
    margin-top:2%;
    margin-left: 2%;
    width: 96%;
    height: auto;
    
}

.stats-table  th td {
 width: 50%;
}
.stats-table th, .stats-table td {
    border: 1px solid #ddd;
    padding: 8px;
}
  .stats-table tr:nth-child(even){background-color: #f2f2f2;}
  .stats-table tr:hover {background-color: #ddd;}
  
  .stats-table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #4c75f2;
    color: white;
  }