@import url('https://fonts.googleapis.com/css?family=Roboto');
.site-layout {
  background-color: #fff;
  width: 100vw;
  height: 100vh;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  display: flex;
}
.siteLayout__body{
  background-color:aliceblue ;
  margin-left:10%;
  margin-top:13vh;
  display: flex;
  height: 85%;
  width: 90%;

}
.flex_body{
  display: inline-flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}
.body_item1{
display: flex;
width: 50%;
height: 100%;
flex-direction: column;
}
.body_item2{
display:flex;
background-color:#ddd3;
width: 50%;
height: 100%;
}
.file-reader-placement{
margin-left:auto;
margin-right:auto;
}

@media (max-width: 769px) {
  .siteLayout__body{
    margin-left: 0;
    width: 100vw;
  }
  .flex_body{
    flex-direction: column;
  }
  .body_item1{
    width: 100%;
    
  }
  .body_item2{
    width: 100%;
  }

}