@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    width: 100%;
    height: 15%;
    display: flex;
    position: fixed;
    align-items: center;
}

.header__content{
    margin: auto auto;
 color: #6c757d;
 font-size: 2em;
 font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.header__content span{
    color: #4c75f2;
}
.toolbar {
    flex-direction: column;
    position: fixed;
    min-width: 10%;
    background: #f6f6f7;
    height: 100%;
    justify-content: flex-start;
    z-index: 99999;
    display: flex;
}
.toolbar__logo{
    -webkit-tap-highlight-color: transparent;
    align-self: center;
    text-transform: uppercase;
    font-weight: 900;
    color: #6c757d;
    text-align: center;
    font-size: .65rem;
    order: 1;
    padding-left: auto;
    padding-top: 2rem;
    padding-bottom: 1rem;
}
.toolbar__logo a {
    text-decoration: none;
    color: inherit;
}
.toolbar__logo span {
    color: #4c75f2;
}

.toolbar__navigation {
    visibility: hidden;
    padding-top: 0vh;
    font-weight: 600;
    font-size: 12px;
    color: #6c757d;
    cursor: pointer;
    transition: all .2s ease-in-out;
    width: 2rem;
    order: 2;

}
.nav-links{
    width: 40%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style: none;
}
@media (max-width: 769px) {
    .toolbar{
    visibility: hidden;
    }
}
.site-layout {
  background-color: #fff;
  width: 100vw;
  height: 100vh;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  display: flex;
}
.siteLayout__body{
  background-color:aliceblue ;
  margin-left:10%;
  margin-top:13vh;
  display: flex;
  height: 85%;
  width: 90%;

}
.flex_body{
  display: inline-flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}
.body_item1{
display: flex;
width: 50%;
height: 100%;
flex-direction: column;
}
.body_item2{
display:flex;
background-color:#ddd3;
width: 50%;
height: 100%;
}
.file-reader-placement{
margin-left:auto;
margin-right:auto;
}

@media (max-width: 769px) {
  .siteLayout__body{
    margin-left: 0;
    width: 100vw;
  }
  .flex_body{
    flex-direction: column;
  }
  .body_item1{
    width: 100%;
    
  }
  .body_item2{
    width: 100%;
  }

}
.donut{
    width: 100%;
    height: 100%;
}

@media (max-width: 769px) {
    .donut{
        margin-top: 5%;
    }
}
.statsTable{
    width: 100%;
    height: 100%;
    display: flex;
    overflow-x: auto;
}
.stats-table{
    margin-top:2%;
    margin-left: 2%;
    width: 96%;
    height: auto;
    
}

.stats-table  th td {
 width: 50%;
}
.stats-table th, .stats-table td {
    border: 1px solid #ddd;
    padding: 8px;
}
  .stats-table tr:nth-child(even){background-color: #f2f2f2;}
  .stats-table tr:hover {background-color: #ddd;}
  
  .stats-table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #4c75f2;
    color: white;
  }
.custom-file-input::-webkit-file-upload-button {
    visibility: hidden;
    color:#fff;
  }
  .custom-file-input::before {
    content: 'Select Chat File';
    display: inline-block;
    background: linear-gradient(top, #f9f9f9, #e3e3e3);
    border: 1px solid #999;
    border-radius: 3px;
    padding: 5px 8px;
    outline: none;
    white-space: nowrap;
    -webkit-user-select: none;
    cursor: pointer;
    text-shadow: 1px 1px #fff;
    font-weight: 700;
    font-size: 10pt;
    margin-top: 10px;
    margin-left:auto;
  }
  .custom-file-input:hover::before {
    border-color: black;
  }
  .custom-file-input:active::before {
    background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
  }

  .file-input{
      opacity: 0;
  }
